import React from 'react';
import {Switch, Route} from 'react-router-dom';
import Home from 'pages/Home';
import Queen from 'pages/Queen';
import Stake from 'pages/Stake';
import BishopRook from 'pages/BishopRook';
import LpPools from 'pages/LpPools';
import NotFound from 'pages/404';
import {ROOT, QUEEN, STAKE, BISHOPROOK, LPPOOLS} from 'router/CONSTANTS';
import BasicLayout from 'layouts/BasicLayout';

const BasicRoute = ({component: Component, ...rest}) => {
    return (
        <Route
            {...rest}
            render={routeProps => (
                <BasicLayout>
                    <Component {...routeProps} />
                </BasicLayout>
            )}
        />
    );
};

export const Router = () => {
    return (
        <Switch>
            {/* List all public routes here */}
            <BasicRoute exact path={ROOT} component={Home}/>
            <BasicRoute exact path={QUEEN} component={Queen}/>
            <BasicRoute exact path={STAKE} component={Stake}/>
            <BasicRoute exact path={BISHOPROOK} component={BishopRook}/>
            <BasicRoute exact path={LPPOOLS} component={LpPools}/>

            {/* List a generic 404-Not Found route here */}
            <BasicRoute path="*">
                <NotFound/>
            </BasicRoute>
        </Switch>
    );
};
