import * as React from 'react';
import Box from '@mui/material/Box';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import InputAdornment from '@mui/material/InputAdornment';
import FormHelperText from '@mui/material/FormHelperText';
import FormControl from '@mui/material/FormControl';
import TextField from '@mui/material/TextField';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import Button from '@mui/material/Button';

export default function InputAdornments() {
  const [values, setValues] = React.useState({
    amount: '',
    password: '',
    weight: '',
    weightRange: '',
    showPassword: false,
  });

  const handleChange = prop => event => {
    setValues({ ...values, [prop]: event.target.value });
  };

  return (
    <Box
      sx={{
        display: 'flex',
        flexWrap: 'wrap',
        paddingTop: '20px',
      }}
    >
      <div>
        <FormControl sx={{ m: 1, width: '25ch' }} variant="outlined">
          <OutlinedInput
            id="outlined-adornment-weight"
            value={values.weight}
            onChange={handleChange('weight')}
            endAdornment={<InputAdornment position="end">ETH</InputAdornment>}
            aria-describedby="outlined-weight-helper-text"
            inputProps={{
              'aria-label': 'weight',
            }}
          />
          <div style={{ flexDirection: 'column' }}>
            <h5>Wallet:0.0 ETH - MAX</h5>
            <h5 style={{ textAlign: 'right' }}>min 0.01 ETH</h5>
          </div>
          <div style={{ justifyContent: 'center', padding: '20px 0' }}>
            <ArrowDownwardIcon />
          </div>
          <div>
            <h1>0.00 Queen</h1>
          </div>
          <Button variant="contained" size="medium" style={{ margin: '20px 0' }}>
            Create
          </Button>
          <div>
            <h5 style={{ textAlign: 'center' }}>Token Queen created will be claimable after 20 Sep, 22:15</h5>
          </div>
        </FormControl>
      </div>
    </Box>
  );
}
