import { div, mul, plus, sub } from '../utils/operation';
import { POOLS } from './pools';
import { endOfDaySub1 } from 'utils/format';

import MerkleTree from 'utils/merkleTree';
import airdrop from 'utils/airdrop.json';

export async function newWeb3Provider(props = {}) {
  let web3Provider;
  const ethereum = await waitForSth(() => window.ethereum, 30);
  // console.log(ethereum.on);
  web3Provider = new Ethers.providers.Web3Provider(ethereum);
  return web3Provider;
}

export const CHAIN_LINK_ORACLE = {
  ETH: '0x639Fe6ab55C921f74e7fac1ee960C0B6293ba612',
  BTC: '0x6ce185860a4963106506C203335A2910413708e9',
};
// export const CHAIN_LINK_ORACLE = {
